import { uploadApi } from "api/upload";
import { orderVisa } from "api/visa";
import React, { useEffect } from "react";
import { Col, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { FormattedMessage, useIntl } from "react-intl";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";

const VisaForm = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    reValidateMode: "onSubmit",
  });
  const {
    empolyee: { specialties },
    meta: { locale },
  } = useSelector((state) => state);
  const intl = useIntl();
  // handler methods
  const submitHandler = async (data) => {
    try {
      const docFile = new FormData();
      docFile.append("files", data.doc_url?.[0]);
      const docFileResponse = await uploadApi(docFile);
      console.log(docFileResponse);
      const response = await orderVisa({
        ...data,
        doc_url: docFileResponse?.[0],
      });
      console.log(response);
    } catch (error) {
      toast.error(error);
    }
  };

  useEffect(() => {
    if (Object.keys(errors).length > 0) {
      toast.error(<FormattedMessage id="fill-required-input" />);
    }
  });

  return (
    <div>
      <form onSubmit={handleSubmit(submitHandler)}>
        <Row style={{ rowGap: "1rem" }}>
          <Col xs={12}>
            <label htmlFor="sponsor_number">
              <p>
                <FormattedMessage id="sponsorNumber" />
              </p>
              <input
                id="sponsor_number"
                name="sponsor_number"
                type={"text"}
                {...register("sponsor_number", { required: true })}
              />
            </label>
          </Col>
          <Col xs={12}>
            <label htmlFor="number_issued">
              <p>
                <FormattedMessage id="outgoingNumber" />
              </p>
              <input
                type="text"
                id="number_issued"
                name="number_issued"
                {...register("number_issued", { required: true })}
              />
            </label>
          </Col>
          <Col xs={12}>
            <label htmlFor="occupation_required">
              <p>
                <FormattedMessage id="Occupation" />
              </p>
              <select
                id="occupation_required"
                name="occupation_required"
                {...register("occupation_required", { required: true })}
              >
                {specialties.map((specialty) => (
                  <option value={specialty?._id}>
                    {locale === "ar" && specialty?.name_ar}
                    {locale === "en" && specialty?.name_en}
                  </option>
                ))}
              </select>
            </label>
          </Col>
          <Col xs={12}>
            <label htmlFor="arrival">
              <p>
                <FormattedMessage id="destination" />
              </p>
              <input
                type="text"
                id="arrival"
                name="arrival"
                {...register("arrival", { required: true })}
              />
            </label>
          </Col>
          <Col xs={12}>
            <label htmlFor="sex">
              <p>
                <FormattedMessage id="ChooseType" />
              </p>
              <select
                id="sex"
                name="sex"
                {...register("sex", { required: true })}
              >
                <option value="">
                  {intl.formatMessage({ id: "ChooseType" })}
                </option>
                <option value="male">
                  {/* {intl.formatMessage({ id: "male" })} */}
                  ذكر
                </option>
              </select>
            </label>
          </Col>
          <Col xs={12}>
            <label htmlFor="number_of_visas">
              <p>
                <FormattedMessage id="numberOfVisas" />
              </p>
              <input
                type="number"
                id="number_of_visas"
                name="number_of_visas"
                {...register("number_of_visas", { required: true })}
              />
            </label>
          </Col>
          <Col xs={12}>
            <label htmlFor="country">
              <p>
                <FormattedMessage id="country" />
              </p>
              <input
                type="text"
                id="country"
                name="country"
                {...register("country", { required: true })}
              />
            </label>
          </Col>
          <Col xs={12}>
            <label htmlFor="doc_url">
              <p>
                <FormattedMessage id="addAttachment" />
              </p>
              <input
                type="file"
                id="doc_url"
                name="doc_url"
                {...register("doc_url", { required: true })}
              />
            </label>
          </Col>
          <Col>
            <input type="submit" value="اضف" />
          </Col>
        </Row>
      </form>
    </div>
  );
};

export default VisaForm;
